import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { useTranslation } from 'next-i18next';
import { motion, AnimatePresence } from 'framer-motion';
import { useScrollDirection } from '@/hooks/useScrollDirection';

const NAV_LINKS = [
  { name: 'nav_books', href: '/books' },
  { name: 'nav_flowers', href: '/flowers' },
  { name: 'nav_media', href: '/media' },
  { name: 'nav_contact', href: '/contact' },
];

export default function Header() {
  const { t } = useTranslation('common');
  const router = useRouter();
  const scrollDirection = useScrollDirection();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);

  // Detect if scrolled for backdrop blur effect
  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Close mobile menu on route change
  useEffect(() => {
    setIsMobileMenuOpen(false);
  }, [router.asPath]);

  return (
    <>
      <motion.header
        initial={{ y: 0 }}
        animate={{ y: scrollDirection === 'down' && !isMobileMenuOpen ? -100 : 0 }}
        transition={{ duration: 0.4, ease: [0.4, 0, 0.2, 1] }}
        className={`fixed top-0 w-full z-50 transition-all duration-500 ${
          isScrolled ? 'bg-background-linen/70 backdrop-blur-lg border-b border-background-beige/50' : 'bg-transparent'
        }`}
      >
        <div className="max-w-[1200px] mx-auto px-6 md:px-10 lg:px-15 h-20 md:h-24 flex justify-between items-center">
          {/* Logo */}
          <Link href="/" data-cursor="pointer" className="text-2xl md:text-[28px] font-display text-text-warm tracking-tight">
            {t('nav_logo', 'Maya')}
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden lg:flex items-center gap-10">
            {NAV_LINKS.map((link) => (
              <NavLink key={link.href} href={link.href} active={router.pathname === link.href}>
                {t(link.name.toLowerCase())}
              </NavLink>
            ))}
          </nav>

          {/* Right Section: Hamburger */}
          <div className="flex items-center gap-6">
            {/* Hamburger Button */}
            <button 
              className="lg:hidden flex flex-col gap-1.5 z-50"
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
            >
              <motion.span 
                animate={isMobileMenuOpen ? { rotate: 45, y: 7.5 } : { rotate: 0, y: 0 }}
                className="w-8 h-0.5 bg-text-warm block origin-center"
              />
              <motion.span 
                animate={isMobileMenuOpen ? { opacity: 0 } : { opacity: 1 }}
                className="w-8 h-0.5 bg-text-warm block"
              />
              <motion.span 
                animate={isMobileMenuOpen ? { rotate: -45, y: -7.5 } : { rotate: 0, y: 0 }}
                className="w-8 h-0.5 bg-text-warm block origin-center"
              />
            </button>
          </div>
        </div>
      </motion.header>

      {/* Mobile Menu Overlay */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-40 bg-background-linen flex flex-col items-center justify-center gap-8"
          >
            {NAV_LINKS.map((link) => (
              <motion.div
                key={link.href}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.1 }}
              >
                <Link 
                  href={link.href} 
                  className={`text-3xl font-serif ${router.pathname === link.href ? 'text-accent-rose' : 'text-text-warm'}`}
                >
                  {t(link.name.toLowerCase())}
                </Link>
              </motion.div>
            ))}
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}

function NavLink({ href, children, active }) {
  return (
    <Link href={href} data-cursor="pointer" className="group relative py-2">
      <span className={`text-[15px] tracking-widest font-medium uppercase transition-colors duration-300 ${
        active ? 'text-accent-rose' : 'text-text-warm/80 group-hover:text-text-warm'
      }`}>
        {children}
      </span>
      {/* Underline Animation */}
      <motion.div 
        className={`absolute bottom-0 left-0 h-[1.5px] bg-accent-sage`}
        initial={false}
        animate={{ width: active ? '100%' : '0%' }}
        whileHover={{ width: '100%' }}
        transition={{ duration: 0.3, ease: "easeInOut" }}
      />
    </Link>
  );
}
